<template>
  <div>
    <div class="top">
      <div class="logobg">
        <img class="logo" src="../../public/images/logo.png" alt />
      </div>
    </div>

    <div class="sections">
      <div class="hash">
        <div>
          <router-link to="login">登录</router-link>
        </div>
        <div>
          <router-link to="reg">注册</router-link>
        </div>
      </div>
      <transition>
        <div class="froms" v-if="flag == true">
          <form>
            <input type="text" placeholder="邮箱/用户名登录" @blur="us" v-model="loginForm.username" />
            <input
              type="password"
              autocomplete="’tel’"
              placeholder="密码"
              @blur="ps"
              v-model="loginForm.password"
            />
          </form>
          <div>
            <button @click="login">登录</button>
          </div>

          <div class="but">
            <span>手机短信登录注册</span>
            <span>立即注册 | 忘记密码？</span>
          </div>

          <div class="line">其它方式登录</div>

          <div class="other">
            <img src="../../public/images/qq.png" alt />
            <img src="../../public/images/wx.png" alt />
            <img src="../../public/images/zfb.png" alt />
          </div>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flag: false,
      loginForm: {
        username: "彭于晏",
        password: "mv123456"
      },
      refs: 0,
      ref: 0
    };
  },
  methods: {
    // 用户名的正则验证
    us() {
      if (this.loginForm.username === "") {
        this.$notify("请输入账号");
      } else {
        this.refs = 1;
      }
    },

    //密码框的正则验证
    ps() {
      let reg = "^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,12}$";
      let req = new RegExp(reg);
      if (this.loginForm.password === "") {
        this.$notify("请输入密码");
      } else if (!req.test(this.loginForm.password)) {
        this.$notify("密码必须6-12位，数字字母组成");
      } else {
        this.ref = 1;
      }
    },
    async login() {
      this.us();
      this.ps();
      if (this.refs === 1 && this.ref === 1) {
        const { data: res } = await this.$http.post(
          "user/login",
          this.loginForm
        );
        if (res.err !== 0) return this.$notify("账号或者密码不正确,请重新输入");
        // 1.将登录成功后的token保存到客户端的sessionStorage
        window.sessionStorage.setItem("token", res.token); //将token保存到sess。。。中
        window.sessionStorage.setItem("username", res.username);

        // 2.通过编程式导航跳转到主页
        this.$router.push(`/`); 
      }
    }
  },
  created() {
    this.flag = true;
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(-200px);
}
.v-enter-active {
  transition: transform 0.3s;
}

//路由切换样式
.sections .hash .router-link-exact-active {
  color: #ff4a4a;
}

.top {
  width: 3.75rem;
  height: 2rem;
  background: url(../../public/images/loginbg.png);
  background-size: cover;
  position: relative;
  margin-bottom: 0.625rem;
  .logobg {
    bottom: -0.42rem;
    left: 50%;
    margin-left: -0.42rem;
    width: 0.84rem;
    height: 0.84rem;
    line-height: 0.74rem;
    background: white;
    position: absolute;
    border-radius: 50%;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.3);
    .logo {
      width: 100%;
      // height: 85%;
    }
  }
}

.sections {
  padding: 0 0.52rem 0.32rem 0.52rem;
  .hash {
    display: flex;
    font-size: 0.15rem;
    margin-bottom: 0.08rem;
    a {
      color: #999;
      font-family: "微软雅黑";
      font-weight: 600;
    }
    div {
      width: 1.355rem;
      text-align: center;
    }
  }

  .froms {
    font-size: 0.1rem;
    padding: 0.285rem 0;
    input {
      padding: 0;
      width: 2.71rem;
      height: 0.36rem;
      border: none;
      outline: none;
      text-indent: 0.235rem;
      border-radius: 0.18rem;
      margin-bottom: 0.16rem;
      box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
    }
    button {
      color: white;
      font-size: 0.16rem;
      width: 100%;
      height: 0.36rem;
      border: none;
      outline: none;
      margin: 0.08rem 0 0.12rem 0;
      border-radius: 0.18rem;
      background: #ff4a4a;
    }
    .but {
      margin-bottom: 0.44rem;
      font-size: 0.08rem;
      display: flex;
      cursor: pointer;
      justify-content: space-between;
      span:first-child {
        color: #ff4a4a;
      }
    }

    .line {
      width: 100%;
      text-align: center;
      color: #999;
      position: relative;
      margin-bottom: 0.3rem;
    }
    .line::after {
      content: "";
      width: 0.83rem;
      height: 1px;
      position: absolute;
      top: 0.06rem;
      right: 0;
      background: #999;
    }
    .line::before {
      content: "";
      width: 0.83rem;
      height: 1px;
      position: absolute;
      left: 0;
      top: 0.06rem;
      background: #999;
    }

    .other {
      padding: 0 0.4rem;
      display: flex;
      justify-content: space-between;
      box-sizing: border-box;
      img {
        width: 0.4rem;
        height: 0.4rem;
      }
    }
  }
}
</style>