<template>
  <div>
    <transition>
      <div class="container" v-if="flag == true">
        <div class="tops">
          <i @click="retuan" class="iconfont icon-gengduo"></i>
        </div>

        <div>
          <img :src="pdData.img" style="display:block" width="100%" alt />
        </div>
        <div class="data">
          <p>
            ￥{{ pdData.price }} 进口休闲零食
            <span>618</span>
          </p>
          <p>{{ pdData.name }}</p>
        </div>

        <div class="main">
          <van-cell @click="showPopup" class="drod">
            <div>
              <p>促销</p>
            </div>
            <div>
              <span class="red">买二赠一 买三赠二</span>
            </div>
            <div>
              <p>折扣</p>
            </div>
            <div>
              <span>
                618当天购买限时
                <b class="red">6</b> 折
              </span>
            </div>
            <div>
              <p>满减</p>
            </div>
            <div>
              <span class="red">满68减18 满618减168</span>
            </div>
            <div>
              <p>选择</p>
            </div>
            <div class="flova">
              <span>原味</span>
            </div>
          </van-cell>
          <van-popup v-model="show" position="bottom" class="contents" :style="{ height: '70%' }">
            <div class="c-title">
              <img :src="pdData.img" alt />
              <div>
                <p>￥{{ pdData.price }}</p>
                <p>{{ desc }}</p>
              </div>
            </div>

            <div class="flovar">
              <p>口味</p>
              <span
                v-for="(item,index) in flovar"
                :key="index"
                :class="{'red':index == current}"
                @click="flovars(index)"
              >{{ item }}</span>
            </div>

            <div class="num">
              选择数量
              <van-stepper v-model="count" theme="round" button-size="22" disable-input />
            </div>

            <van-goods-action>
              <van-goods-action-button @click="addCart" type="warning" text="加入购物车" />
              <van-goods-action-button @click="purchase(Data)" type="danger" text="立即购买" />
            </van-goods-action>
          </van-popup>
        </div>

        <div class="main">
          <div class="b-title">
            <div @click="cl2">
              <p>
                评价（{{ commentData.length }}+）
                <span>查看全部</span>
              </p>
              <div v-if="fitstData.username !== ''">
                <p>
                  <img :src="fitstData.head" alt />
                </p>
                {{ fitstData.username }}
              </div>
              <div>{{ fitstData.content }}</div>
            </div>
          </div>
          <van-popup v-model="plNum" position="bottom" class="contents" :style="{ height: '80%' }">
            <div class="pl">
              <div class="plBtn">
                <van-field v-model="pl1" rows="1" autosize type="textarea" placeholder="请输入评论内容" />
                <button class="push" @click="pushComment">发送评论</button>
              </div>
              <div class="title">
                <div :class="{'red':0 == state}" @click="pl">全部({{ commentData.length }}+)</div>
                <div :class="{'red':1 == state}" @click="pl2">好评({{ commentData1.length }}+)</div>
                <div :class="{'red':2 == state}" @click="pl3">中评({{ commentData2.length }}+)</div>
                <div :class="{'red':3 == state}" @click="pl4">差评({{ commentData3.length }}+)</div>
              </div>
              <div v-if="state == 0">
                <div class="com" v-for="(item,index) in commentData" :key="index">
                  <div class="head">
                    <div>
                      <img :src="item.head" alt />
                    </div>
                    <p>{{ item.username }}</p>
                  </div>
                  <div class="contents">
                    <div>
                      <van-rate v-model="item.score" readonly />
                    </div>
                    <div style="margin: 20px 0;">{{ item.content }}</div>
                    <div class="time">
                      <p>口味:{{ item.flavor }}</p>
                      <p>{{ item.time }}</p>
                    </div>
                  </div>
                </div>
              </div>
              <div v-if="state == 1">
                <div class="com" v-for="(item,index) in commentData1" :key="index">
                  <div class="head">
                    <div>
                      <img :src="item.head" alt />
                    </div>
                    <p>{{ item.username }}</p>
                  </div>
                  <div class="contents">
                    <div>
                      <van-rate v-model="item.score" readonly />
                    </div>
                    <div style="margin: 20px 0;">{{ item.content }}</div>
                    <div class="time">
                      <p>口味:{{ item.flavor }}</p>
                      <p>{{ item.time }}</p>
                    </div>
                  </div>
                </div>
              </div>
              <div v-if="state == 2">
                <div class="com" v-for="(item,index) in commentData2" :key="index">
                  <div class="head">
                    <div>
                      <img :src="item.head" alt />
                    </div>
                    <p>{{ item.username }}</p>
                  </div>
                  <div class="contents">
                    <div>
                      <van-rate v-model="item.score" readonly />
                    </div>
                    <div style="margin: 20px 0;">{{ item.content }}</div>
                    <div class="time">
                      <p>口味:{{ item.flavor }}</p>
                      <p>{{ item.time }}</p>
                    </div>
                  </div>
                </div>
              </div>
              <div v-if="state == 3">
                <div class="com" v-for="(item,index) in commentData3" :key="index">
                  <div class="head">
                    <div>
                      <img :src="item.head" alt />
                    </div>
                    <p>{{ item.username }}</p>
                  </div>
                  <div class="contents">
                    <div>
                      <van-rate v-model="item.score" readonly />
                    </div>
                    <div style="margin: 20px 0;">{{ item.content }}</div>
                    <div class="time">
                      <p>口味:{{ item.flavor }}</p>
                      <p>{{ item.time }}</p>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </van-popup>
        </div>

        <div class="detaily">
          <div>商品详情</div>
        </div>

        <div class="main" style="background:white">
          <div class="details" v-if="num == 0">
            <div>
              <img :src="pdData.details" alt />
            </div>
          </div>
        </div>

        <guesslike></guesslike>

        <van-goods-action>
          <van-goods-action-icon icon="cart-o" @click="goCart" text="购物车" />
          <van-goods-action-icon :icon="star" text="收藏" @click="coll" color="#ff5000" />
          <van-goods-action-button @click="addCart" type="warning" text="加入购物车" />
          <van-goods-action-button type="danger" @click="purchase(Data)" text="立即购买" />
        </van-goods-action>
      </div>
    </transition>
  </div>
</template>

<script>
import guesslike from "../components/guesslike";
import { Toast } from "vant";
import { ImagePreview } from "vant";
export default {
  data() {
    return {
      flag: false,
      star: "star-o", //收藏
      pdData: {}, //商品数据
      show: false,
      plNum: false,
      count: 1,
      Data: [],
      state: 0,
      pl1: "",
      myorderDatas: [],
      fitstData: {
        head: "",
        username: "",
        content: "暂无评论"
      },
      commentData: [],
      commentData1: [],
      commentData2: [],
      commentData3: [],
      userData: {},
      num: 0,
      current: 0, //口味的索引
      desc: "选择 口味",
      flovar: ["原味", "青瓜味", "番茄味", "烧烤味"],
      myflavor: "" //口味最终的选择
    };
  },
  methods: {
    //返
    retuan() {
      window.history.back();   
    },

    //加载数据
    async load() {
      const { data: res } = await this.$http.post("product/key", {
        _id: this.$route.params.id
      });
      this.myflavor = this.flovar[0];
      this.Data = res.data;
      this.pdData = res.data[0];
      this.addhistory();
    },

    async getUser() {
      const { data: res } = await this.$http.post("user/myusername", {
        username: window.sessionStorage.getItem("username")
      });
      this.userData = res.data[0];
    },

    async getColl() {
      const { data: res } = await this.$http.post("collection/keys", {
        pdid: this.pdData_id
      });

      if (res.data !== "") {
        this.star = "star";
      }
    },

    async getOrder() {
      const { data: res } = await this.$http.post("order/usname", {
        username: window.sessionStorage.getItem("username")
      });
      if (res.err !== 0) return this.$msg.error("页面获取错误，请重新进入网页");
      for (let i = 0; i < res.data.length; i++) {
        //判断是否买了该产品，否则不能评论
        if (res.data[i].state == 2) {
          if (res.data[i].pdid == this.$route.params.id) {
            this.myorderDatas = res.data[i];

            this.flag = true;
          }
        }
      }
    },

    async getComment() {
      this.commentData = [];
      this.commentData1 = [];
      this.commentData2 = [];
      this.commentData3 = [];
      const { data: res } = await this.$http.post("comment/key", {
        pdid: this.$route.params.id
      });

      if (res.data.length !== 0) {
        this.fitstData = res.data[0];
      }
      for (let i = 0; i < res.data.length; i++) {
        if (res.data[i].score <= 2) {
          this.commentData3.push(res.data[i]);
        } else if (res.data[i].score == 3) {
          this.commentData2.push(res.data[i]);
        } else if (res.data[i].score >= 4) {
          this.commentData1.push(res.data[i]);
        }
      }
      this.commentData = res.data;
    },

    async pushComment() {
      if (this.flag == false) return Toast("请购买商品并评价后再来评论哦");
      if (this.myorderDatas.score == null)
        return Toast("请在订单里给星星再来评论哦");

      const { data: res } = await this.$http.post("comment/add", {
        username: window.sessionStorage.getItem("username"),
        pdid: this.pdData._id,
        flavor: this.myorderDatas.flavor,
        content: this.pl1,
        head: this.userData.img,
        score: this.myorderDatas.score
      });

      this.getComment();
      this.pl1 = "";
      if (res.err == 0) return Toast("评论成功");
    },

    pl() {
      this.state = 0;
    },

    pl2() {
      this.state = 1;
    },

    pl3() {
      this.state = 2;
    },

    pl4() {
      this.state = 3;
    },

    //显示选择
    showPopup() {
      this.show = true;
    },

    flovars(index) {
      this.current = index;
      this.myflavor = this.flovar[index];
    },

    //记录到浏览记录
    async addhistory() {
      const { data: res } = await this.$http.post("history/add", {
        pdid: this.pdData._id,
        name: this.pdData.name,
        desc: this.pdData.desc,
        img: this.pdData.img,
        username: window.sessionStorage.getItem("username")
      });
    },

    //记录到浏览记录
    async coll() {
      const { data: res } = await this.$http.post("collection/add", {
        pdid: this.pdData._id,
        name: this.pdData.name,
        desc: this.pdData.desc,
        img: this.pdData.img,
        username: window.sessionStorage.getItem("username")
      });
      this.star = "star";
    },

    //加入购物车
    async addCart() {
      const { data: res } = await this.$http.post("cart/shoppingcart", {
        username: window.sessionStorage.getItem("username"),
        pdid: this.pdData._id,
        count: this.count,
        flavor: this.myflavor
      });
      Toast(`加入成功`);
      if (res.err === -3) return this.$msg("商品已经在购物车了哦");
    },

    //立即购买
    purchase(row) {
      row[0].flavor = this.myflavor;
      row[0].count = this.count;
      row[0].img = this.pdData.img;
      let arr = JSON.stringify(row);
      this.$router.push("/order/" + encodeURIComponent(arr));
    },

    goCart() {
      this.$router.push("/cart");
    },

    cl1() {
      this.num = 0;
    },

    cl2() {
      this.plNum = true;
    }
  },
  created() {
    this.load();
    this.flag = true;
    this.addhistory();
    this.getUser();
    this.getOrder();
    this.getComment();
    this.getColl();
  },

  components: {
    guesslike
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

.tops {
  top: 0.2rem;
  left: 0.2rem;
  width: 0.25rem;
  height: 0.25rem;
  text-align: center;
  font-size: 0.13rem;
  z-index: 10;
  position: fixed;
  border-radius: 50%;
  line-height: 0.25rem;
  transform: rotate(-180deg);
  background: rgba(0, 0, 0, 0.5);
  i {
    color: white;
    font-size: 0.16rem;
  }
}

.data {
  border-radius: 0 0 0.08rem 0.08rem;
  background: white;
  box-sizing: border-box;
  padding: 0.14rem 0.12rem;
  p:first-child {
    color: #ff4a4a;
    font-weight: 800;
    font-size: 0.14rem;
    span {
      color: white;
      background: #ff4a4a;
      border-radius: 0.08rem;
      padding: 0rem 0.06rem;
      margin-left: 0.1rem;
    }
  }
  p:last-child {
    color: #333;
    font-size: 0.18rem;
    margin-top: 0.12rem;
  }
}

.drod {
  p,
  span {
    color: #999;
  }
  .red {
    color: #ff4a4a;
  }
}

.c-title {
  display: flex;
  div {
    padding-left: 0.2rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    p {
      width: 100%;
    }
    p:first-of-type {
      color: #ff4a4a;
      font-size: 0.2rem;
    }
    p:last-of-type {
      color: #333;
      font-size: 0.1rem;
    }
  }
  img {
    width: 40%;
    display: block;
    border-radius: 0.08rem;
  }
}

.details {
  img {
    width: 100%;
  }
}

.flovar {
  margin: 0.2rem 0;
  display: flex;
  flex-wrap: wrap;
  padding: 0.12rem 0;
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;
  p {
    width: 100%;
    font-size: 0.14rem;
  }

  .red {
    color: white;
    background: #ff4a4a;
  }

  span {
    margin: 0.12rem 0 0 0.12rem;
    font-size: 0.12rem;
    background: #f5f5f5;
    padding: 0.1rem 0.2rem;
    border-radius: 0.08rem;
  }
}

.num {
  display: flex;
  margin: 0.2rem 0;
  line-height: 0.25rem;
  padding: 0.12rem 0;
  font-size: 0.14rem;
  border-top: 1px solid #ededed;
  border-bottom: 1px solid #ededed;
  justify-content: space-between;
}

.b-title {
  margin-bottom: 0.2rem;
  > div {
    padding: 0.12rem;
    border-radius: 0.08rem;
    box-sizing: border-box;
    background: white;
    > div:first-of-type {
      color: #999;
      display: flex;
      align-items: center;
      margin: 0.12rem 0 0 0;
      font-size: 0.16rem;
      p {
        width: 0.5rem;
        height: 0.5rem;
        overflow: hidden;
        border-radius: 50%;
        margin-right: 0.12rem;
        img {
          height: 100%;
          float: left;
        }
      }
    }
    > div:last-of-type {
      color: #999;
      margin: 0.12rem 0;
      font-size: 0.16rem;
    }
    > p {
      color: #999;
      font-size: 0.16rem;
      display: flex;
      justify-content: space-between;
      span {
        color: #ff4a4a;
      }
    }
  }
}

.plBtn {
  display: flex;
  margin: 0.12rem 0;
  div {
    width: 80%;
  }
  button {
    width: 1.1rem;
    float: left;
  }
}

.push {
  color: #ff4a4a;
  border: none;
  outline: none;
  font-size: 0.16rem;
}

.detaily {
  color: #ff4a4a;
  width: 100%;
  height: 0.5rem;
  line-height: 0.5rem;
  text-align: center;
  font-size: 0.16rem;
  background: white;
}

.pl {
  margin-bottom: 1rem;
}

.com {
  display: flex;
  padding: 0.12rem;
  background: white;
  margin-bottom: 0.12rem;
  box-sizing: border-box;
  .head {
    width: 0.7rem;
    overflow: hidden;
    div {
      width: 0.64rem;
      height: 0.64rem;
      overflow: hidden;
      border-radius: 50%;
      img {
        float: left;
        height: 100%;
      }
    }
    p {
      font-size: 0.14rem;
      text-align: center;
      margin: 0.12rem 0;
    }
  }
  .contents {
    width: 2.6rem;
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    padding: 0 0 0rem 0.12rem;
    div {
      width: 100%;
    }
    div:nth-child(1) {
      height: 0.2rem;
      display: flex;
    }
    div:nth-child(2) {
      font-size: 0.14rem;
    }
    div:nth-child(3) {
      display: flex;
      width: 2.3rem;
      font-size: 0.12rem;
      justify-content: space-between;
      p {
        color: #999;
      }
    }
  }
}

.title {
  width: 100%;
  display: flex;
  padding: 0.12rem 0;
  margin: 0.2rem 0;
  font-size: 0.14rem;
  background: white;
  justify-content: space-around;
  .red {
    color: #ff4a4a;
  }
}
</style>