<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
  <div>
    <div class="top">
      购物车
      <span @click="removeSelect">删除已经选中</span>
    </div>

    <div class="main">
      <div v-for="(item,index) in myCart" :key="index">
        <div class="checked">
          <van-checkbox @change="activeChecked" v-model="item.state"></van-checkbox>
        </div>
        <div class="img">
          <img :src="item.img" alt />
        </div>
        <div class="text">
          <p class="title">{{ item.name }}</p>
          <p class="desc">{{ item.desc }}</p>
          <p class="price">
            <span>￥{{ item.price }}</span>
            <van-stepper
              @change="handleChange(myCart,index)"
              v-model="item.counts"
              theme="round"
              button-size="22"
              disable-input
            />
          </p>
        </div>
      </div>
      <p class="end">讨厌，都被你看光了~</p>
    </div>

    <div class="bottom">
      <div class="allcheck">
        <span>
          <input v-model="state" @change="stateAll" type="checkbox" class="Checkbox" id="check1" />
          <label for="check1"></label>
        </span>
        全选
      </div>

      <div class="result">
        <p>
          <span>合计:</span>
          ￥{{ filte }}
        </p>
        <button @click="result">确认下单</button>
      </div>
    </div>

    <Menu></Menu>
  </div>
  </van-pull-refresh>
</template>

<script>
import Menu from "../components/Menu";
import { Toast } from 'vant';

export default {
  data() {
    return {
      checked: true,
      isLoading: false,
      myCart: [],
      count: 1,
      state: true,
      total: 999,
      disabled: false,
    };
  },
  computed: {
    filte(){
      return this.total.toFixed(2)
    },
  },
  methods: {
    onRefresh() {
      this.getCart();
      setTimeout(() => {
        Toast("刷新成功");
        this.isLoading = false;
      }, 1000);
    },

    async getCart() {
      const { data: res } = await this.$http.post("cart/num", {
        username: window.sessionStorage.getItem("username")
      });
      console.log(res)
      let pdid,counts;
      for (let i = 0; i < res.data.length; i++) {
        pdid = res.data[i]._id
        counts = res.data[i].count
        var { data: req } = await this.$http.post("product/key", {
          _id: res.data[i].pdid
        });
        res.data[i] = req.data[0]
        res.data[i].pdid = pdid
        res.data[i].counts = counts
      }
      this.myCart = res.data;
    },

    //全选
    stateAll() {
      this.myCart.forEach(item => {
        item.state = this.state;
      });
      this.disableds();
      this.judge(); //调用计算函数
    },

     //计算
    cat() {
      setTimeout(() => {
        this.judge();
        if (this.myCart.length == 0) {
          this.piece = 0;
          this.total = 0;
          this.disabled = true;
        }
      }, 100);
    },

    //商品数量的改变
    async handleChange(row, index) {
      const { data: res } = await this.$http.post("cart/update", {
        _id: row[index].pdid,
        count: row[index].counts
      });
      this.judge();
    },

    //单选
    activeChecked() {
      this.disableds();
      this.judge();
      const result = this.file(this.myCart, true);
      if (result.length !== this.myCart.length) {
        this.state = false;
      } else {
        this.state = true;
      }
    },

    //删除选中的商品
    removeSelect() {
      this.myCart.forEach(async (item, index) => {
        console.log(item)
        if (item.state == true) {
          const { data: res } = await this.$http.post("cart/del", {
            _id: item.pdid
          });
          if (res.err == 0) {
            Toast(`删除成功`)
            this.cat();
            this.getCart();
            console.log(item)
          } else {
            Toast("删除失败");
          }
        }
      });
    },

    //复用方法，过滤出符合条件的值
    file(data, index) {
      return data.filter(item => {
        //filter判断数组中是否存在某个值
        return item.state == index;
      });
    },

    //判断是否全部选中，如果没选中就不能结算
    disableds() {
      let res = this.file(this.myCart, true);
      if (res.length == 0) {
        this.disabled = true;
      } else {
        this.disabled = false;
      }
    },

    //计算商品选中或不选择
    judge() {
      let num = 0,
        count = 0;
      this.myCart.forEach(item => {
        if (item.state == true) {
          item.total = item.counts * item.price;
          num = item.counts + num;
          this.piece = num;
          count = item.total + count;
          count.toFixed(2);
          this.total = count;
        }else {
          this.total = count
        }
      });
    },

    result() {
      if(this.myCart.length == 0) return Toast("请先添加商品!");
      let res = this.file(this.myCart, true);
      let arr=JSON.stringify(res)
      
      console.log(this.myCart)
      this.$router.push('/order/'+encodeURIComponent(arr))
    }
  },
  created() {
    this.getCart();
    setTimeout(this.judge,300);
    this.cat()
  },
  components: {
    Menu
  }
};
</script>

<style lang="less" scoped>
.top {
  top: 0;
  z-index: 2;
  position: fixed;
  span {
    font-size: 0.013rem;
    position: absolute;
    right: 0.12rem;
    top: 0rem;
  }
}

.main {
  margin: 0.57rem 0 1.3rem 0;
}

.main > div {
  width: 100%;
  display: flex;
  height: 1.08rem;
  padding: 0.12rem;
  background: white;
  align-items: center;
  box-sizing: border-box;
  border-radius: 0.08rem;
  margin-bottom: 0.12rem;
  .checked {
    height: 100%;
    display: flex;
    width: 0.2rem;
    align-items: center;
  }
  .img {
    width: 0.84rem;
    height: 0.84rem;
    overflow: hidden;
    margin: 0 0.12rem;
    border-radius: 0.08rem;
    img {
      height: 100%;
      float: left;
    }
  }
  .text {
    width: 2.05rem;
    height: 100%;
    .title {
      color: #333;
      font-size: 0.16rem;
      margin: 0.05rem 0;
    }
    .desc {
      color: #999;
      font-size: 0.13rem;
      margin: 0.05rem 0 0.225rem 0;
    }

    .price {
      color: #ff4a4a;
      font-size: 0.18rem;
      display: flex;
      justify-content: space-between;
      div {
        display: inline-block;
        .van-stepper__input {
          margin: 0;
        }
      }
      span {
        float: left;
      }
    }
  }
}

.bottom {
  width: 3.75rem;
  display: flex;
  bottom: 0.5rem;
  position: fixed;
  height: 0.49rem;
  padding: 0 0.12rem;
  align-items: center;
  background: white;
  box-sizing: border-box;
  justify-content: space-between;
  border-radius: 0.08rem 0.08rem 0 0;
  .allcheck {
    font-size: 0.12rem;
    text-indent: 0.25rem;
    > span {
      top: -0.02rem;
      left: -0.2rem;
      position: relative;
    }
    .Checkbox {
      position: absolute;
      visibility: hidden;
    }
    .Checkbox + label {
      position: absolute;
      width: 0.14rem;
      height: 0.14rem;
      border: 1px solid #a6a6a6;
      border-radius: 50%;
      background-color: #ddd;
    }
    .Checkbox:checked + label:after {
      content: "";
      position: absolute;
      left: 2px;
      top: 2px;
      width: 9px;
      height: 4px;
      border: 2px solid #424242;
      border-top-color: transparent;
      border-right-color: transparent;
      transform: rotate(-45deg);
      -ms-transform: rotate(-60deg);
      -moz-transform: rotate(-60deg);
      -webkit-transform: rotate(-60deg);
    }
  }
  .result {
    width: 2rem;
    display: flex;
    height: 0.325rem;
    align-items: center;
    justify-content: space-between;
    p {
      color: #ff4a4a;
      font-size: 0.16rem;
      span {
        color: #333;
      }
    }
    button {
      width: 1rem;
      border: none;
      height: 100%;
      outline: none;
      color: white;
      font-size: 0.13rem;
      border-radius: 0.16rem;
      background: linear-gradient(to right, #ff4f4a, #ff6f4a);
    }
  }
}
</style>