<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <transition>
      <div class="container" v-if="flag == true">
        <div class="top">
          <i @click="retuan" class="iconfont icon-gengduo"></i>
          热卖
        </div>

        <div>
          <img width="100%" src="../../public/images/hotbanner.jpg" alt />
        </div>

        <div class="main">
          <div class="products">
            <div v-for="(item,index) in hotData" @click="seedetails(hotData,index)" :key="index">
              <span class="tops">TOP{{ index+1 }}</span>
              <div>
                <div>
                  <img :src="item.img" alt />
                </div>
              </div>
              <p class="title">{{ item.name }}</p>
              <p class="desc">{{ item.desc }}</p>
              <p class="addcart">
                <b style="font-size:0.12rem">￥</b>{{ item.price }}
                <b style="font-size:0.13rem;color:#999"> 销量99+</b>
                <span
                  class="iconfont icon-dilanxianxingiconyihuifu_huabanfuben2"
                  @click.stop="addCart(hotData,index)"
                ></span>
              </p>
            </div>
          </div>
        </div>

        <p class="end">就只剩这么多咯~</p>

        <Menu></Menu>
      </div>
    </transition>
  </van-pull-refresh>
</template>

<script>
import Menu from "../components/Menu";
import { Toast } from "vant";
import { ImagePreview } from "vant";
export default {
  data() {
    return {
      flag: false,
      isLoading: false,
      hotData: []
    };
  },
  methods: {
    onRefresh() {
      this.myData();
      setTimeout(() => {
        Toast("刷新成功");
        this.isLoading = false;
      }, 1000);
    },

     //通过编程式导航跳转到商品详情
    seedetails(row, index) {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$notify("请先登录");
      } else {
        this.$router.push(`/details/1/${row[index]._id}`);
      }
    },

    //获取用户数据
    async myData() {
      const { data: res } = await this.$http.post("product/getinfopage", {
        pagesize: 20,
        page: 1
      });
      console.log(res);
      this.hotData = res.info.list;
    },

    //加入购物车
    async addCart(hotData, index) {
      const { data: res } = await this.$http.post("cart/shoppingcart", {
        username: window.sessionStorage.getItem("username"),
        pdid: hotData[index]._id,
        count: this.count,
        flavor: '原味'
      });
      if (res.err === 0) return Toast(`加入购物车成功`);
      if (res.err === -3) return Toast(`商品已经在购物车了哦`);
    },

    //返
    retuan() {
      window.history.back();  
    }
  },
  components: {
    Menu
  },
  created() {
    this.flag = true;
    this.myData();
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

.top {
  top: 0;
  z-index: 2;
  position: fixed;
  span {
    font-size: 0.013rem;
    position: absolute;
    right: 0.12rem;
    top: 0rem;
  }
}

.products {
    width: 100%;
    margin-bottom: 0.7rem;
    .tops {
      top: 0;
      left: 0.12rem;
      color: white;
      background: #ff4a4a;
      font-size: 0.12rem;
      position: absolute;
      border-radius: 0 0 0.04rem 0.04rem;
      padding: 0.03rem 0.06rem;
    }

    > div:nth-child(1) {
      padding: 0.15rem 0.1rem;
    }
    > div {
      width: 3.51rem;
      padding: 0.1rem;
      height: 1.09rem;
      overflow: hidden;
      margin-bottom: 0.12rem;
      border-radius: 0.08rem;
      position: relative;
      background-color: white;
      box-sizing: border-box;
    }

    p {
      margin: 0.1rem;
      padding-left: 0.95rem;
      box-sizing: border-box;
    }
    .title {
      font-size: 0.16rem;
      color: #333;
    }
    .desc {
      font-size: 0.12rem;
      color: #9a9a9a;
    }
    .addcart {
      bottom: 0rem;
      position: absolute;
      width: 3.1rem;
      font-size: 0.18rem;
      line-height: 0.28rem;
      color: #ff4a4a;
      span {
        color: white;
        float: right;
        width: 0.28rem;
        height: 0.28rem;
        text-align: center;
        border-radius: 50%;
        font-size: 0.15rem;
        background: #ff4a4a;
      }
    }

    div div {
      width: 0.935rem;
      float: left;
      img {
        width: 100%;
        height: 0.9rem;
        display: block;
        border-radius: 0.08rem;
      }
    }
  }
</style>