<template>
  <div>
    <transition>
      <div class="container" v-if="flag == true">
        <div class="top">
          <i @click="retuan" class="iconfont icon-gengduo"></i>地址
        </div>
        <div class="main">
          <van-address-list
            v-model="chosenAddressId"
            @select="selects"
            :list="list"
            default-tag-text="默认"
            @add="onAdd"
            @edit="onEdit"
          />
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flag: false,
      userData: [],
      chosenAddressId: "0",
      list: []
    };
  },
  methods: {
    //获取用户数据
    async myData() {
      const { data: res } = await this.$http.post("address/get", {
        username: window.sessionStorage.getItem("username")
      });
      for (let i = 0; i < res.data.length; i++) {
        res.data[i].id = i;
        if(res.data[i].isDefault == true) {
          this.chosenAddressId = res.data[i].id
        }
      }
      this.list = res.data;
    },
    //返
    retuan() {
      window.history.back();  
    },
    selects(item, index) {
      console.log(item);
      this.chosenAddressId = index;
    },
    onAdd() {
      this.$router.push("/addaddress");
    },
    onEdit(item, index) {
      this.$router.push(`/setaddress/${item._id}`);
    }
  },
  created() {
    this.flag = true;
    this.myData();
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

</style>