<template>
  <div>
    <div class="index-navbar main">
      <div>
        <van-search
          class="search"
          v-model="search"
          shape="round"
          @change="searchs"
          @focus="num = 1"
          background="#ff4a4a"
          placeholder="搜索商品类目"
        />
        <span @click="retuan">取消</span>
      </div>
    </div>
    <div class="historys" v-if="num == 0">
      <div class="titles">历史记录</div>

      <div v-for="(item,index) in data" :key="index" @click.stop="searchEvent" class="list">
        {{ item }}
        <span @click.stop="del(data,index)">x</span>
      </div>
    </div>

    <div v-if="num == 1" class="data">
      <div class="products">
        <div v-for="(item,index) in pdData" @click="seedetails(pdData,index)" :key="index">
          <div>
            <div>
              <img :src="item.img" alt />
            </div>
          </div>
          <p class="title">{{ item.name }}</p>
          <p class="desc">{{ item.desc }}</p>
          <p class="addcart">
            <b style="font-size:0.12rem">￥</b>
            {{ item.price }}
            <b style="font-size:0.13rem;color:#999">销量30+</b>
            <span
              class="iconfont icon-dilanxianxingiconyihuifu_huabanfuben2"
              @click.stop="addCart(pdData,index)"
            ></span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from "vant";
import { ImagePreview } from "vant";
export default {
  data() {
    return {
      search: "",
      num: 0,
      data: '',
      pdData: []
    };
  },
  methods: {
    async searchs() {
      const { data: res } = await this.$http.post("product/getinfokey", {
        key: this.search
      });
      this.pdData = res.list;
      console.log(res);
      localStorage.search = JSON.stringify(this.search);
      if (this.searchs == "") {
        this.num = 0;
      }
    },

    //删除历史记录
    del(row, index) {
      console.log(row);
      // row.splice(index, 1);
      // localStorage.search = JSON.stringify(this.search);
    },

    searchEvent() {
      this.search = this.data[0]
      this.searchs();
      this.num = 1
    },

    //通过编程式导航跳转到商品详情
    seedetails(row, index) {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$notify("请先登录");
      } else {
        this.$router.push(`/details/1/${row[index]._id}`);
      }
    },

    async addCart(productData, index) {
      const { data: res } = await this.$http.post("cart/shoppingcart", {
        username: window.sessionStorage.getItem("username"),
        pdid: productData[index]._id,
        count: this.count,
        flavor: "原味"
      });
      if (res.err === 0) return Toast(`加入购物车成功`);
      if (res.err === -3) return Toast(`商品已经在购物车了哦`);
    },

    //返
    retuan() {
      window.history.back();
    }
  },
  mounted() {
    this.flag = true;
    this.data = localStorage.search ? [JSON.parse(localStorage.search)] : "";
    console.log(this.search);
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

// 顶部导航
.index-navbar {
  width: 3.75rem;
  height: 0.44rem;
  background-color: #ff4a4a;
  .search {
    width: 100%;
    color: #999;
  }
  span {
    color: white;
    width: 0.5rem;
    line-height: 0.35rem;
    text-align: center;
    display: inline-block;
    font-size: 0.14rem;
  }
}

.historys {
  width: 3.75rem;
  height: 70%;
  background: white;
  position: absolute;
  padding: 0.12rem;
  box-sizing: border-box;
  bottom: 0;
  z-index: 10000;
  .titles {
    font-size: 0.16rem;
  }

  .list {
    font-size: 0.13rem;
    margin: 0.2rem 0;
    display: flex;
    justify-content: space-between;
  }
}

.data {
  width: 3.75rem;
  height: 85%;
  background: white;
  position: fixed;
  padding: 0.12rem;
  box-sizing: border-box;
  bottom: 0;
  z-index: 10000;
  .products {
    width: 100%;
    margin-bottom: 0.7rem;
    > div:nth-child(1) {
      padding: 0.15rem 0.1rem;
    }
    > div {
      width: 3.51rem;
      padding: 0.1rem;
      height: 1.09rem;
      overflow: hidden;
      margin-bottom: 0.12rem;
      border-radius: 0.08rem;
      position: relative;
      background-color: #f5f5f5;
      box-sizing: border-box;
    }

    p {
      margin: 0.1rem;
      padding-left: 0.95rem;
      box-sizing: border-box;
    }
    .title {
      margin: 0 0 0.1rem 0.1rem;
      font-size: 0.16rem;
      color: #333;
    }
    .desc {
      font-size: 0.12rem;
      color: #9a9a9a;
    }
    .addcart {
      bottom: 0rem;
      position: absolute;
      width: 3.1rem;
      font-size: 0.18rem;
      line-height: 0.28rem;
      color: #ff4a4a;
      span {
        color: white;
        float: right;
        width: 0.28rem;
        height: 0.28rem;
        text-align: center;
        border-radius: 50%;
        font-size: 0.15rem;
        background: #ff4a4a;
      }
    }

    div div {
      width: 0.935rem;
      float: left;
      img {
        width: 100%;
        height: 0.9rem;
        display: block;
        border-radius: 0.08rem;
      }
    }
  }
}

.end {
  margin-top: 2rem;
}
</style>