<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <transition>
      <div class="container" v-if="flag == true">
        <div class="top">
          <i @click="retuan" class="iconfont icon-gengduo"></i>
          我的收藏
          <span @click="removeSelect">删除已经选中</span>
        </div>
        <div class="main">
          <div v-for="(item,index) in collData" :key="index" @click="details(collData,index)">
            <div class="checked">
              <van-checkbox @change="activeChecked" v-model="item.state"></van-checkbox>
            </div>
            <div class="img">
              <img :src="item.img" alt />
            </div>
            <div class="text">
              <p class="title">{{ item.name }}</p>
              <p class="desc">{{ item.desc }}</p>
              <p class="price">
                <span>￥{{ item.price }}</span>
                <van-stepper
                  @change="handleChange(myCart,index)"
                  v-model="item.counts"
                  theme="round"
                  button-size="22"
                  disable-input
                />
              </p>
            </div>
          </div>
        </div>

        <p class="end">就只剩这么多咯~</p>
      </div>
    </transition>
  </van-pull-refresh>
</template>

<script>
import { Toast } from "vant";
import { ImagePreview } from "vant";
export default {
  data() {
    return {
      flag: false,
      isLoading: false,
      collData: []
    };
  },
  methods: {
    onRefresh() {
      this.myData();
      setTimeout(() => {
        Toast("刷新成功");
        this.isLoading = false;
      }, 1000);
    },

    details(row,index) {
      this.$router.push(`/details/1/${row[index]._id}`);
    },

    //复用方法，过滤出符合条件的值
    file(data, index) {
      return data.filter(item => {
        //filter判断数组中是否存在某个值
        return item.state == index;
      });
    },

    //单选
    activeChecked() {
      const result = this.file(this.collData, true);
      if (result.length !== this.collData.length) {
        this.state = false;
      } else {
        this.state = true;
      }
    },

    //删除选中的商品
    removeSelect() {
      this.collData.forEach(async (item, index) => {
        if (item.state == true) {
          const { data: res } = await this.$http.post("collection/del", {
            _id: item.pdid
          });
          if (res.err == 0) {
            Toast(`删除成功`);
            this.myData();
          } else {
            Toast("删除失败");
          }
        }
      });
    },

    //获取用户数据
    async myData() {
      const { data: res } = await this.$http.post("collection/num", {
        username: window.sessionStorage.getItem("username")
      });
      let pdid;
      for (let i = 0; i < res.data.length; i++) {
        pdid = res.data[i]._id;
        var { data: req } = await this.$http.post("product/key", {
          _id: res.data[i].pdid
        });
        res.data[i] = req.data[0];
        res.data[i].pdid = pdid;
      }
      console.log(res)
      this.collData = res.data;
    },
    //返
    retuan() {
      window.history.back();  
    }
  },
  created() {
    this.flag = true;
    this.myData();
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

.top {
  top: 0;
  z-index: 2;
  position: fixed;
  span {
    font-size: 0.013rem;
    position: absolute;
    right: 0.12rem;
    top: 0rem;
  }
}

.main {
  margin: 0.57rem 0 1.3rem 0;
}

.main > div {
  width: 100%;
  display: flex;
  height: 1.08rem;
  padding: 0.12rem;
  background: white;
  align-items: center;
  box-sizing: border-box;
  border-radius: 0.08rem;
  margin-bottom: 0.12rem;
  .checked {
    height: 100%;
    display: flex;
    width: 0.2rem;
    align-items: center;
  }
  .img {
    width: 0.84rem;
    height: 0.84rem;
    overflow: hidden;
    margin: 0 0.12rem;
    border-radius: 0.08rem;
    img {
      height: 100%;
      float: left;
    }
  }
  .text {
    width: 2.05rem;
    height: 100%;
    .title {
      color: #333;
      font-size: 0.16rem;
      margin: 0.05rem 0;
    }
    .desc {
      color: #999;
      font-size: 0.13rem;
      margin: 0.05rem 0 0.225rem 0;
    }

    .price {
      color: #ff4a4a;
      font-size: 0.18rem;
      display: flex;
      justify-content: space-between;
      div {
        display: inline-block;
        .van-stepper__input {
          margin: 0;
        }
      }
      span {
        float: left;
      }
    }
  }
}
</style>