<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <transition>
      <div class="container" v-if="flag == true">
        <div class="top">
          <i @click="retuan" class="iconfont icon-gengduo"></i>
          我的订单
        </div>

        <div class="title">
          <div
            v-for="(item,index) in title"
            :key="index"
            @click="seleorder(index)"
            :class="{'current':index == current}"
          >{{ item }}</div>
        </div>

        <div class="main">
          <div style="margin-top: 1rem">
            <div v-if="current == 0">
              <div v-for="(item,index) in myorderDatas" class="pd" :key="index">
                <div class="box">
                  <div>
                    <img :src="item.img" alt />
                  </div>
                  <div>
                    <p>{{ item.name }}</p>
                    <p style="color:#999">{{ item.desc }}</p>
                  </div>

                  <div>
                    <p>{{ item.states }}</p>
                    <span style="color:#333">x{{ item.count }}</span>
                  </div>
                </div>
                <p>
                  总价￥99.00,优惠￥0.00 实付款￥
                  <span>99.00</span>
                </p>
                <div class="bot">
                  <button
                    class="actives"
                    @click="uporder(myorderDatas,index)"
                    v-if="item.state == 1"
                  >确认收货</button>
                  <button class="actives" @click="seeorder = true" v-if="item.state == 2">评价</button>
                </div>

                <van-popup v-model="seeorder" position="bottom" :style="{ height: '30%' }">
                  <div class="p-title">
                    发表评价
                    <span style="color: #ff4a4a" @click="pushcom(myorderDatas,index)">发布</span>
                  </div>
                  <van-field
                    v-model="message"
                    rows="1"
                    autosize
                    type="textarea"
                    placeholder="宝贝满足你的期待吗？说下你的使用心得，分享给想买的他们吧"
                  />
                  <van-rate
                    v-model="item.score"
                    @change="pf(myorderDatas,index)"
                    style="float:left; margin:0.12rem 0 0 0.12rem"
                  />
                </van-popup>
              </div>
            </div>
          </div>
          <div>
            <div v-if="current == 1">
              <div v-for="(item,index) in myorderDatas3" class="pd" :key="index">
                <div class="box">
                  <div>
                    <img :src="item.img" alt />
                  </div>
                  <div>
                    <p>{{ item.name }}</p>
                    <p style="color:#999">{{ item.desc }}</p>
                  </div>

                  <div>
                    <p>{{ item.states }}</p>
                    <span style="color:#333">x{{ item.count }}</span>
                  </div>
                </div>
                <p>
                  总价￥99.00,优惠￥0.00 实付款￥
                  <span>99.00</span>
                </p>
                <div class="bot">
                  <button class="actives" v-if="item.state == 1">确认收货</button>
                  <button class="actives" v-if="item.state == 2">评价</button>
                </div>
              </div>
            </div>
          </div>
          <div>
            <div v-if="current == 2">
              <div v-for="(item,index) in myorderDatas1" class="pd" :key="index">
                <div class="box">
                  <div>
                    <img :src="item.img" alt />
                  </div>
                  <div>
                    <p>{{ item.name }}</p>
                    <p style="color:#999">{{ item.desc }}</p>
                  </div>

                  <div>
                    <p>{{ item.states }}</p>
                    <span style="color:#333">x{{ item.count }}</span>
                  </div>
                </div>
                <p>
                  总价￥99.00,优惠￥0.00 实付款￥
                  <span>99.00</span>
                </p>
                <div class="bot">
                  <button
                    class="actives"
                    @click="uporder(myorderDatas1,index)"
                    v-if="item.state == 1"
                  >确认收货</button>
                  <button class="actives" v-if="item.state == 2">评价</button>
                </div>
              </div>
            </div>
          </div>
          <div>
            <div v-if="current == 3">
              <div v-for="(item,index) in myorderDatas2" class="pd" :key="index">
                <div class="box">
                  <div>
                    <img :src="item.img" alt />
                  </div>
                  <div>
                    <p>{{ item.name }}</p>
                    <p style="color:#999">{{ item.desc }}</p>
                  </div>

                  <div>
                    <p>{{ item.states }}</p>
                    <span style="color:#333">x{{ item.count }}</span>
                  </div>
                </div>
                <p>
                  总价￥99.00,优惠￥0.00 实付款￥
                  <span>99.00</span>
                </p>
                <div class="bot">
                  <button class="actives" v-if="item.state == 1">确认收货</button>
                  <button class="actives" v-if="item.state == 2">评价</button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </transition>
  </van-pull-refresh>
</template>

<script>
import { Toast } from "vant";
import { ImagePreview } from "vant";
export default {
  data() {
    return {
      flag: false,
      num: false,
      isLoading: false,
      current: 0,
      title: ["全部", "待发货", "待收货", "待评价"],
      myorderDatas: [],
      myorderDatas1: [],
      myorderDatas2: [],
      myorderDatas3: [],
      message: "",
      seeorder: false,
      userData: {}
    };
  },
  methods: {
    onRefresh() {
      this.getOrderList();
      setTimeout(() => {
        Toast("刷新成功");
        this.isLoading = false;
      }, 1000);
    },

    seleorder(index) {
      this.current = index;
    },

    //获取订单列表
    async getOrderList() {
      this.myorderDatas = [];
      this.myorderDatas1 = [];
      this.myorderDatas2 = [];
      this.myorderDatas3 = [];
      const { data: res } = await this.$http.post("order/usname", {
        username: window.sessionStorage.getItem("username")
      });
      if (res.err !== 0) return this.$msg.error("页面获取错误，请重新进入网页");
      for (let i = 0; i < res.data.length; i++) {
        if (res.data[i].state == 1) {
          res.data[i].states = "卖家已发货";
          this.myorderDatas1.push(res.data[i]);
        } else if (res.data[i].state == 2) {
          res.data[i].states = "交易成功";
          this.myorderDatas2.push(res.data[i]);
        } else if (res.data[i].state == 3) {
          res.data[i].states = "买家已付款";
          this.myorderDatas3.push(res.data[i]);
        }
      }
      this.myorderDatas = res.data;
      console.log(res.data);
    },

    async getUser() {
      const { data: res } = await this.$http.post("user/myusername", {
        username: window.sessionStorage.getItem("username")
      });
      this.userData = res.data[0];
    },

    async uporder(row, index) {
      const { data: res } = await this.$http.post("order/upstate", {
        _id: row[index]._id,
        state: "2"
      });
      this.getOrderList();
      if (res.err == 0) return Toast("确认收货成功");
    },

    async pf(row, index) {
      const { data: res } = await this.$http.post("order/score", {
        _id: row[index]._id,
        score: row[index].score
      });
      this.num = true;
    },

    async pushcom(row, index) {
      if (this.num == true) {
        const { data: res } = await this.$http.post("comment/add", {
          username: window.sessionStorage.getItem("username"),
          pdid: row[index].pdid,
          flavor: row[index].flavor,
          content: this.message,
          head: this.userData.img,
          score: row[index].score
        });
        console.log(res)
        if (res.err == 0) {
          Toast("评价成功");
          this.seeorder = false
        }
      }
    },

    //返
    retuan() {
      window.history.back();  
    }
  },
  created() {
    this.flag = true;
    this.current = this.$route.params.index;
    this.getUser();
    this.getOrderList();
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

.top {
  top: 0;
  z-index: 2;
  position: fixed;
  span {
    font-size: 0.013rem;
    position: absolute;
    right: 0.12rem;
    top: 0rem;
  }
}

.title {
  width: 100%;
  display: flex;
  padding: 0.2rem 0;
  z-index: 10;
  top: 0.4rem;
  background: #f5f5f5;
  position: fixed;
  justify-content: space-around;
  > div {
    color: #333;
    font-size: 0.16rem;
  }
  .current {
    color: #ff4a4a;
    position: relative;
  }
  .current::after {
    content: "";
    bottom: -0.15rem;
    left: 0%;
    width: 100%;
    height: 0.03rem;
    background: #ff4a4a;
    position: absolute;
  }
}

.pd {
  margin-top: 0.12rem;
  padding: 0.12rem;
  box-sizing: border-box;
  border-radius: 0.08rem;
  background: white;
  position: relative;
  .box {
    display: flex;
    > div:first-of-type {
      img {
        width: 0.8rem;
        border-radius: 0.08rem;
      }
    }
    > div:nth-child(2) {
      color: #333;
      text-indent: 0.1rem;
      font-size: 0.14rem;
      p {
        width: 100%;
        margin-bottom: 0.12rem;
      }
    }
    > div:last-of-type {
      top: 0.1rem;
      color: #ff4a4a;
      right: 0.1rem;
      line-height: 0.2rem;
      font-size: 0.13rem;
      position: absolute;
    }
  }
  > p {
    color: #333;
    font-size: 0.12rem;
    text-align: right;
  }
  .bot {
    width: 100%;
    display: flex;
    margin: 0.12rem 0 0 0;
    justify-content: flex-end;
    button {
      outline: none;
      padding: 0.07rem 0.15rem;
      border: 1px solid #666;
      border-radius: 0.16rem;
      font-size: 0.13rem;
      background: white;
      margin-right: 0.05rem;
    }
    .actives {
      color: #ff4a4a;
      border: 1px solid #ff4a4a;
    }
  }
}

.p-title {
  width: 100%;
  height: 0.5rem;
  color: #999;
  display: flex;
  padding: 0 0.12rem;
  font-size: 0.16rem;
  align-items: center;
  box-sizing: border-box;
  justify-content: space-between;
}
</style>