          </div>
        </div>
      </van-sticky>

      <!-- 中部轮播区域 -->
      <div class="main-content">
        <div class="swipe">
          <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
            <van-swipe-item v-for="(item,index) in mainList" :key="index" class="lis">
              <img :src="item.img" alt />
            </van-swipe-item>
          </van-swipe>
        </div>
        <div class="main-icon">
          <div @click="hots">
            <p></p>
            <p>热卖折扣</p>
          </div>
          <div>
            <p></p>
            <p>当季新品</p>
          </div>
          <div>
            <p></p>
            <p>每日折扣</p>
          </div>
          <div>
            <p></p>
            <p>礼包专区</p>
          </div>
        </div>
      </div>

      <div class="main">
        <div class="news">
          <div class="title">
            <span>新闻简报</span>
            <van-notice-bar left-icon="volume-o" text="ZEROFOOD官方商城上线啦，快来享打折优惠！" />
          </div>
          <div class="selection">
            <div>
              <img src="../../public/images/pin.png" alt />
            </div>
            <div>
              <img src="../../public/images/xin.png" alt />
              <img src="../../public/images/tuan.png" alt />
            </div>
          </div>
        </div>
      </div>

      <!-- 新品上市 -->
      <div class="newProducts main">
        <h3>
          新品上市
          <span>
            更多
            <i class="iconfont icon-gengduo"></i>
          </span>
        </h3>
        <div class="products">
          <div
            v-for="(item,index) in newProducts"
            @click="seedetails(newProducts,index)"
            :key="index"
          >
            <div>
              <div>
                <img :src="item.img" alt />
              </div>
            </div>
            <p class="title">{{ item.name }}</p>
            <p class="desc">{{ item.desc }}</p>
            <p class="addcart">
              <b style="font-size:0.12rem">￥</b>
              {{ item.price }}
              <b style="font-size:0.13rem;color:#999">销量30+</b>
              <span
                class="iconfont icon-dilanxianxingiconyihuifu_huabanfuben2"
                @click.stop="addCart(newProducts,index)"
              ></span>
            </p>
          </div>
        </div>
      </div>

      <!-- 底部菜单栏 -->
      <Menu></Menu>
    </div>
  </van-pull-refresh>
