<script>
// @ is an alias to /src
import { Toast } from "vant";
import { ImagePreview } from "vant";
import Menu from "../components/Menu";

export default {
  name: "Index",
  data() {
    return {
      search: "",
      count: 1,
      mainList: [],
      newProducts: [],
      isLoading: false
    };
  },
  methods: {
    onRefresh() {
      this.getMainList();
      this.getNewProducts();
      setTimeout(() => {
        Toast("刷新成功");
        this.isLoading = false;
      }, 1000);
    },
    //获取banner
    async getMainList() {
      const { data: res } = await this.$http.post("banner/class", {
        typename: "simobanner"
      });
      this.mainList = res.data;
    },

    //获取产品
    async getNewProducts() {
      const { data: res } = await this.$http.post("product/all");
      this.newProducts = res.data;
    },

    //通过编程式导航跳转到商品详情
    seedetails(row, index) {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$notify("请先登录");
      } else {
        this.$router.push(`/details/1/${row[index]._id}`);
      }
    },

    hots() {
      this.$router.push(`/hot`);
    },

    async addCart(productData, index) {
      const { data: res } = await this.$http.post("cart/shoppingcart", {
        username: window.sessionStorage.getItem("username"),
        pdid: productData[index]._id,
        count: this.count,
        flavor: "原味"
      });
      if (res.err === 0) return Toast(`加入购物车成功`);
      if (res.err === -3) return Toast(`商品已经在购物车了哦`);
    },

    searchPage() {
      this.$router.push("/search");
    }
  },
  created() {
    this.getMainList();
    this.getNewProducts();
  },
  components: {
    Menu //菜单栏组件
  }
};
</script>

