<template>
  <div>
    <transition>
      <div class="container" v-if="flag == true">
        <div class="top">
          <i @click="retuan" class="iconfont icon-gengduo"></i>账户设置
        </div>
        <div class="main">
          <div class="personal">
            <div class="users" @click="user">
              <div>
                <img :src="userData.img" alt />
              </div>
              <div>
                <p>{{ userData.username }}</p>
                <p>用户名:{{ userData.username }}</p>
              </div>
              <div>
                <i class="iconfont icon-gengduo"></i>
              </div>
            </div>
            <p class="line"></p>
            <div class="address" @click="address">
              地址管理
              <i class="iconfont icon-gengduo"></i>
            </div>
          </div>

          <div class="Privacy">
            <div @click="security">
              账户与安全
              <i class="iconfont icon-gengduo"></i>
            </div>
            <p class="line"></p>
            <div @click="privacy">
              隐私策略
              <i class="iconfont icon-gengduo"></i>
            </div>
            <p class="line"></p>
            <div>
              通用
              <i class="iconfont icon-gengduo"></i>
            </div>
          </div>

          <div class="we">
            <div @click="feedback">
              问题反馈
              <i class="iconfont icon-gengduo"></i>
            </div>
            <p class="line"></p>
            <div @click="about">
              关于手机ZEROFOOD
              <i class="iconfont icon-gengduo"></i>
            </div>
          </div>

          <div class="swich">切换账号</div>

          <div class="swich" @click="out">退出登录</div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flag: false,
      userData: []
    };
  },
  methods: {
    //获取用户数据
    async myData() {
      const { data: res } = await this.$http.post("user/getinfouser", {
        key: window.sessionStorage.getItem("username")
      });
      this.userData = res.list[0];
    },
    retuan() {
      window.history.back();  
    },
    user() {
      this.$router.push("/person");
    },

    address() {
      this.$router.push("/address");
    },

    security() {
      this.$router.push("/security");
    },

    about() {
      this.$router.push("/about");
    },

    feedback() {
      this.$router.push("/feedback");
    },

    privacy() {
      this.$router.push("/privacy");
    },

    out() {
      window.sessionStorage.clear();
      this.$router.push("/login");
    }
  },
  created() {
    this.flag = true;
    this.myData();
  }
};
</script>

