}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

//路由切换样式
.sections .hash .router-link-exact-active {
  color: #ff4a4a;
}

.line {
  width: 100%;
  height: 0.01rem;
  background: #ddd;
}
.personal {
  width: 100%;
  height: 1.5rem;
  border-radius: 0.08rem;
  background: white;
  box-sizing: border-box;
  .users {
    width: 100%;
    height: 1.125rem;
    box-sizing: border-box;
    padding: 0.24rem 0.12rem 0.24rem 0.2rem;
    > div:nth-child(1) {
      float: left;
      width: 0.64rem;
      height: 0.64rem;
      overflow: hidden;
      border-radius: 50%;
      img {
        float: left;
        height: 0.64rem;
      }
    }
    > div:nth-child(2) {
      float: left;
      height: 0.5rem;
      width: 2.1rem;
      line-height: 0.25rem;
      padding: 0.08rem 0 0.08rem 0.12rem;
      box-sizing: border-box;
      p:first-child {
        font-size: 0.16rem;
      }
      p:last-child {
        font-size: 0.16rem;
      }
    }
    > div:nth-child(3) {
      height: 0.6rem;
      display: flex;
      align-items: center;
      i {
        // float: left;
        font-size: 0.32rem;
      }
    }
  }
  .address {
    height: 0.37rem;
    display: flex;
    padding: 0 0.12rem;
    line-height: 0.37rem;
    font-size: 0.13rem;
    justify-content: space-between;
  }
}

.Privacy {
  width: 100%;
  height: 1.125rem;
  padding: 0 0.12rem;
  background: white;
  margin-top: 0.12rem;
  box-sizing: border-box;
  border-radius: 0.08rem;
  div {
    display: flex;
    height: 0.37rem;
    font-size: 0.13rem;
    line-height: 0.37rem;
    justify-content: space-between;
  }
}

.we {
  width: 100%;
  height: 0.75rem;
  padding: 0 0.12rem;
  background: white;
  margin-top: 0.12rem;
  box-sizing: border-box;
  border-radius: 0.08rem;
  div {
    height: 0.37rem;
    display: flex;
    font-size: 0.13rem;
    align-items: center;
    justify-content: space-between;
  }
}

.swich {
  width: 100%;
  height: 0.4rem;
  text-align: center;
  font-size: 0.16rem;
  line-height: 0.4rem;
  background: white;
  margin-top: 0.12rem;
  border-radius: 0.08rem;
