<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <div>
      <div class="bg"></div>
      <div class="mains main">
        <div class="pelpeo">
          <div class="pp">
            <!-- <van-sticky :offset-top="50"> -->
            <div class="setuser" title="设置">
              <i class="iconfont icon-shezhi" @click="set"></i>
            </div>
            <!-- </van-sticky> -->
            <div class="tx">
              <img :src="img" @click="seper" alt />
            </div>
            <div class="usname">
              <p v-if="flag == false">{{ userData.username }}</p>
              <p @click="login" v-else-if="flag == true">登录/注册</p>
              <p>积分：0</p>
            </div>
          </div>
          <div class="gList">
            <div @click="coll">
              <p>{{ collNum }}</p>
              <p>收藏商品</p>
            </div>
            <div @click="follow">
              <p>0</p>
              <p>店铺关注</p>
            </div>
            <div>
              <p>0</p>
              <p>喜欢的内容</p>
            </div>
            <div @click="history">
              <p>{{ historyNum }}</p>
              <p>浏览历史</p>
            </div>
          </div>
        </div>

        <div class="order">
          <div class="order-tel" @click="allorder(0)">
            <span>我的订单</span>
            <span>
              查看全部订单
              <i class="iconfont icon-gengduo"></i>
            </span>
          </div>
          <div class="order-box">
            <div @click="allorder(0)">
              <p>
                <i class="iconfont icon-daifukuan"></i>
              </p>
              <p>全部 </p>
            </div>
            <div @click="allorder(1)">
              <p>
                <i class="iconfont icon-daifahuo"></i>
              </p>
              <p>待发货</p>
            </div>
            <div @click="allorder(2)">
              <p>
                <i class="iconfont icon-daishouhuo"></i>
              </p>
              <p>待收货</p>
            </div>
            <div @click="allorder(3)">
              <p>
                <i class="iconfont icon-daipingjia"></i>
              </p>
              <p>待评价</p>
            </div>
          </div>
        </div>
      </div>

      <guesslike></guesslike>
    </div>
    <Menu></Menu>
  </van-pull-refresh>
</template>

<script>
import Menu from "../components/Menu";
import guesslike from '../components/guesslike'
import { Toast } from "vant";
import { ImagePreview } from "vant";

export default {
  data() {
    return {
      username: "登录/注册",
      flag: false,
      page: 1,
      pagesize: 16,
      isLoading: false,
      userData: [],
      img: "",
      historyNum: 0,
      collNum: 0,
    };
  },
  methods: {
    onRefresh() {
      this.myData();
      setTimeout(() => {
        Toast("刷新成功");
        this.isLoading = false;
      }, 1000);
    },

    async myData() {
      const { data: res } = await this.$http.post("user/getinfouser", {
        key: window.sessionStorage.getItem("username")
      });
      this.userData = res.list[0];
      this.img = this.userData.img;
      this.historys();
      this.colls();
    },

    async historys() {
      const { data: res } = await this.$http.post("history/num", {
        username: window.sessionStorage.getItem("username")
      });
      this.historyNum = res.data.length;
    },

      async colls() {
      const { data: res } = await this.$http.post("collection/num", {
        username: window.sessionStorage.getItem("username")
      });
      this.collNum = res.data.length;
    },

    //查看头像
    seper() {
      ImagePreview([this.userData.img]);
    },

    load() {
      if (window.sessionStorage.getItem("token") === null) {
        this.flag = true;
      } else {
        this.username = window.sessionStorage.getItem("username");
      }
    },

    login() {
      this.$router.push("/login");
    },

    allorder(index) {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$notify("请先登录");
      } else {
        this.$router.push(`/allorder/${index}`);
      }
    },

    follow() {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$notify("请先登录");
      } else {
        this.$router.push(`/follow`);
      }
    },

    
    coll() {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$notify("请先登录");
      } else {
        this.$router.push(`/collection`);
      }
    },

    set() {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$notify("请先登录");
      } else {
        this.$router.push("/setuserdata");
      }
    },

    history() {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$notify("请先登录");
      } else {
        this.$router.push("/history");
      }
    }
  },
  created() {
    this.load();
    let token = window.sessionStorage.getItem("token");
    if (!token) {
      this.$notify("请先登录");
    } else {
      this.myData();
    }
  },
  components: {
    Menu,
    guesslike
  }
};
</script>

<style lang="less" scoped>
.bg {
  width: 3.75rem;
  background: url(../../public/images/loginbg.png) no-repeat;
  background-size: cover;
  height: 2rem;
}

.mains {
  position: absolute;
  top: 0;
}

.pelpeo {
  z-index: 1;
  width: 100%;
  height: 1.2rem;
  margin-top: 0.4rem;
  border-radius: 0.08rem;
  background: white;
}

.pp {
  height: 0.6rem;
  position: relative;
  box-sizing: border-box;
  padding: 0.08rem 0.2rem 0.12rem 0.2rem;
  .setuser {
    display: flex;
    i {
      font-size: 0.22rem;
    }
    height: 0.22rem;
    position: absolute;
    top: 0.12rem;
    right: 0.12rem;
  }
  .tx {
    top: -0.16rem;
    width: 0.64rem;
    height: 0.64rem;
    overflow: hidden;
    border-radius: 50%;
    position: absolute;
    img {
      float: left;
      height: 100%;
      height: 0.64rem;
    }
  }
  .usname {
    margin-left: 0.84rem;
    p:first-of-type {
      margin-bottom: 0.08rem;
    }
    p:last-of-type {
      font-size: 0.08rem;
      margin-bottom: 0.08rem;
      color: #ff4a4a;
    }
    p {
      font-size: 0.16rem;
    }
  }
}

.gList {
  display: flex;
  height: 0.59rem;
  padding: 0.12rem 0;
  align-items: center;
  box-sizing: border-box;
  border-top: 0.01rem solid #ddd;
  div {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    width: 0.875rem;
    height: 100%;
  }
  p:first-of-type {
    color: #ff4a4a;
    font-size: 0.24rem;
  }
  p:last-of-type {
    color: #999;
    font-size: 0.12rem;
  }
  p {
    width: 100%;
    text-align: center;
  }
}

.order {
  width: 100%;
  height: 1.13rem;
  margin-top: 0.12rem;
  border-radius: 0.08rem;
  background: white;
  .order-tel {
    width: 100%;
    height: 0.39rem;
    line-height: 0.39rem;
    display: flex;
    box-sizing: border-box;
    padding: 0 0.12rem;
    justify-content: space-between;
    border-bottom: 0.01rem solid #ddd;
    span:first-of-type {
      font-size: 0.15rem;
    }
    span:last-of-type {
      color: #999;
      font-size: 0.15rem;
    }
    span {
      font-size: 0.12rem;
    }
  }

  .order-box {
    display: flex;
    flex-wrap: wrap;
    align-content: space-between;
    height: 0.75rem;
    line-height: 0.25rem;
    div {
      width: 0.875rem;
      height: 100%;
      padding: 0.12rem 0;
      box-sizing: border-box;
      p:first-of-type {
        color: #ff4a4a;
        font-size: 0.24rem;
        .iconfont {
          color: #ff4a4a;
          font-size: 0.24rem;
        }
      }
      p:last-of-type {
        color: #999;
        font-size: 0.12rem;
      }
      p {
        width: 100%;
        text-align: center;
      }
    }
  }
}

</style>