<template>
  <transition>
    <div v-if="flag == true">
      <div class="top">
        <i @click="retuan" class="iconfont icon-gengduo"></i>确认订单
      </div>

      <div class="main">
        <van-cell @click="showPopup" class="seleAdd">
          <div class="icon">
            <van-icon name="location" />
          </div>
          <div class="add">
            <p>
              {{ addressList.name }}
              <span>{{ addressList.tel }}</span>
            </p>
            <p>{{ addressList.address }}</p>
            <p>收货不便时，可选择暂存服务</p>
          </div>
          <div class="rightIcon">
            <i class="iconfont icon-gengduo"></i>
          </div>
        </van-cell>
        <van-popup v-model="show" position="bottom" class="contents" :style="{ height: '50%' }">
          <van-address-list
            v-model="chosenAddressId"
            @select="selects"
            :list="list"
            @add="onAdd"
            @edit="onEdit"
          />
        </van-popup>
      </div>

      <div class="main" style="margin-bottom: 1rem">
        <div class="pd" v-for="(item,index) in rowList" :key="index">
          <div class="p-title">
            <img :src="item.img" alt />
            <div class="desc">
              <p>{{ item.name }}</p>
              <p>
                口味分类：{{ item.flavor }}
                <span>产品规格：500g</span>
              </p>
              <p>发货时间，付款后48小时内</p>
            </div>
            <div class="price">￥{{ item.price }}</div>
          </div>
          <div class="num orders">
            <span>购买数量</span>
            <van-stepper @change="comput" v-model="item.count" />
          </div>
          <div class="bot">
            <p>配送方式</p>
            <div>
              <span>普通配送</span>
              <p>
                快递 免邮
                <i @click="retuan" class="iconfont icon-gengduo"></i>
              </p>
            </div>
          </div>
          <div class="bot">
            <p>运费险</p>
            <div>
              <span>卖家赠送，退换货可赔</span>
              <p>
                <i @click="retuan" class="iconfont icon-gengduo"></i>
              </p>
            </div>
          </div>
          <div class="bot">
            <p>订单备注</p>
            <div>
              <span>选填，请先和商家协商一致</span>
              <p></p>
            </div>
          </div>
          <div class="bots">
            <b>共{{item.count}}件</b>
            小计：
            <span>￥{{ item.count * item.price }}</span>
          </div>
        </div>
      </div>
      <div class="fiexd">
        <b>共{{ count }}件</b>
        合计：
        <span>{{ price }}</span>
        <button @click="submits">确认下单</button>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  data() {
    return {
      flag: false,
      show: false,
      chosenAddressId: "0",
      price: 0,
      count: 0,
      list: [],
      addressList: {},
      rowList: [],
      time: ""
    };
  },
  methods: {
    //获取用户数据
    async myAddData() {
      const { data: res } = await this.$http.post("address/get", {
        username: window.sessionStorage.getItem("username")
      });
      for (let i = 0; i < res.data.length; i++) {
        res.data[i].id = i;
        if (res.data[i].isDefault == true) {
          this.chosenAddressId = res.data[i].id;
          this.addressList = res.data[0];
        }
      }
      this.list = res.data;
    },

    //返
    retuan() {
      window.history.back();  
    },

    showPopup() {
      this.show = true;
    },

    selects(item, index) {
      this.addressList = item;
    },
    onAdd() {
      this.$router.push("/addaddress");
    },
    onEdit(item, index) {
      this.$router.push(`/setaddress/${item._id}`);
    },

    comput() {
      let count = 0;
      let price = 0;
      let total = 0;
      this.rowList.forEach(item => {
        item.price = new Number(item.price);
        total = item.count * item.price;
        count += item.count;
        price += total;
      });
      this.count = count;
      this.price = price;
      if (this.rowList.length < 2) {
        this.price = this.rowList[0].count * this.rowList[0].price;
      }
    },

    async or() {
      const { data: res } = await this.$http.post("or/add", {
        username: window.sessionStorage.getItem("username"),
        time: this.time
      });
    },

    times() {
      this.time = new Date().getTime();
    },

    handle() {
      this.rowList.forEach(item => {
        item.total = item.count * item.price;
        item.username = window.sessionStorage.getItem("username");
        item.address = this.addressList._id;
        item.order = new Date().getTime();
        item.ordername = this.addressList.name;
        item.orderid = this.time;
        item.pdid = item._id;
      });
    },

    dels() {
      this.rowList.forEach(async item => {
        const { data: res } = await this.$http.post("cart/del", {
          _id: item.pdid
        });
      });
    },

    //提交
    async submits() {
      this.dels();
      this.or();
      this.handle();
      let arr = JSON.stringify(this.rowList);
      for (let i = 0; i < this.rowList.length; i++) {
        const { data: res } = await this.$http.post(
          "order/add",
          this.rowList[i]
        );
      }
      this.$router.push(`/success/${this.time}/${encodeURIComponent(arr)}`);
    }
  },
  created() {
    this.flag = true;
    this.myAddData();
    var list = decodeURIComponent(this.$route.params.obj);
    this.rowList = JSON.parse(list);
    this.comput();
    this.times();
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

.pd {
  margin-top: 0.12rem;
  padding: 0.12rem;
  background: white;
  border-radius: 0.08rem;
  .p-title {
    display: flex;
    font-size: 0.16rem;
    img {
      width: 30%;
      display: block;
    }
    .desc {
      width: 1.8rem;
      padding-left: 0.12rem;
      box-sizing: border-box;
      p:first-child {
        color: #333;
        font-size: 0.16rem;
        margin: 0.1rem 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
      p:nth-child(2) {
        width: 1rem;
        color: #999;
        font-size: 0.1rem;
        background: #fafafa;
        border-radius: 0.08rem;
        padding: 0.05rem 0.15rem;
        span {
          display: block;
          margin-top: 0.05rem;
        }
      }
      p:last-child {
        color: #ff4a4a;
        font-size: 0.13rem;
        margin: 0.15rem 0;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
    .price {
      display: flex;
      align-items: center;
      color: #ff4a4a;
    }
  }
  .num {
    display: flex;
    margin: 0.2rem 0;
    justify-content: space-between;
    span {
      width: 0.9rem;
      line-height: 0.25rem;
      text-align: right;
      display: inline-block;
      font-size: 0.13rem;
    }
  }
  .bot {
    display: flex;
    margin: 0.2rem 0;
    font-size: 0.13rem;
    justify-content: space-between;
    > p {
      width: 0.9rem;
      text-align: right;
    }
    div {
      width: 2.25rem;
      display: flex;
      justify-content: space-between;
      span {
        color: #999;
      }
      p {
      }
    }
  }
  .bots {
    text-align: right;
    font-size: 0.13rem;
    b {
      color: #999;
    }
    span {
      color: #ff4a4a;
      font-size: 0.18rem;
    }
  }
}

.fiexd {
  bottom: 0;
  width: 100%;
  display: flex;
  height: 0.5rem;
  position: fixed;
  padding: 0 0.12rem;
  align-items: center;
  justify-content: flex-end;
  box-sizing: border-box;
  background: white;
  font-size: 0.13rem;
  b {
    color: #999;
  }
  span {
    color: #ff4a4a;
    font-size: 0.16rem;
    margin-right: 0.15rem;
  }
  button {
    width: 1rem;
    border: none;
    outline: none;
    color: white;
    color: white;
    height: 0.33rem;
    font-size: 0.13rem;
    border-radius: 0.16rem;
    background: linear-gradient(to right, #ff4f4a, #ff6f4a);
  }
}
</style>