<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <transition>
      <div class="container" v-if="flag == true">
        <div class="top">
          <i @click="retuan" class="iconfont icon-gengduo"></i>
          关注的店铺
        </div>
        <p class="end"> 你还没有关注有店铺哦~~ </p>
      </div>
    </transition>
  </van-pull-refresh>
</template>

<script>
import { Toast } from "vant";
import { ImagePreview } from "vant";
export default {
  data() {
    return {
      flag: false,
      isLoading: false
    };
  },
  methods: {
    onRefresh() {
      this.myData();
      setTimeout(() => {
        Toast("刷新成功");
        this.isLoading = false;
      }, 1000);
    },
    //返
    retuan() {
      window.history.back();  
    }
  },
  created() {
    this.flag = true;
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

.top {
  top: 0;
  z-index: 2;
  position: fixed;
  span {
    font-size: 0.013rem;
    position: absolute;
    right: 0.12rem;
    top: 0rem;
  }
}

.end {
  margin-top: 2rem;
}
</style>