<template>
  <section>
    <div class="guesslike main">
      <h3>
        猜你喜欢
        <span>
          更多
          <i class="iconfont icon-gengduo"></i>
        </span>
      </h3>
      <div class="products">
        <div v-for="(item,index) in guesslike" :key="index" @click="seedetails(guesslike,index)">
          <div>
            <img :src="item.img" alt />
            <p class="title">{{ item.name }}</p>
            <p class="desc">{{ item.desc }}</p>
          </div>
          <p class="addcart">
            ￥{{ item.price }}
            <span @click.stop="addCart(guesslike,index)">添加购物车</span>
          </p>
        </div>
      </div>
    </div>
    <div class="end">到底了~宝贝</div>
  </section>
</template>

<script>
export default {
  data() {
    return {
      guesslike: []
    };
  },
  methods: {
    seedetails(row, index) {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$notify("请先登录");
      } else {
        this.$router.push(`/details/1/${row[index]._id}`);
      }
    },
    async getguesslike() {
      const { data: res } = await this.$http.post("product/getinfopage", {
        typename: "新品上市",
        page: this.page,
        pagesize: this.pagesize
      });
      this.guesslike = res.info.list;
    },

    async addCart(productData, index) {
      const { data: res } = await this.$http.post("cart/shoppingcart", {
        username: window.sessionStorage.getItem("username"),
        pdid: productData[index]._id
      });
      if (res.err === 0) return Toast(`加入购物车成功`);
      if (res.err === -3) return Toast(`商品已经在购物车了哦`);
    }
  },
  created() {
    this.getguesslike();
  }
};
</script>

<style lang="less" scoped>
.guesslike {
  margin-top: 1.2rem;
  h3 {
    width: 100%;
    font-size: 0.14rem;
    font-weight: 500;
    margin-bottom: 0.09rem;
    span {
      float: right;
    }
  }
  .products {
    width: 100%;
    column-count: 2;
    column-gap: 0.12rem;
    break-inside: auto;
    > div:nth-child(1) {
      padding: 0.15rem 0.1rem;
    }
    > div {
      // float: left;
      width: 1.7rem;
      padding: 0.1rem;
      height: 100%;
      overflow: auto;
      margin-bottom: 0.12rem;
      border-radius: 0.08rem;
      background-color: white;
      box-sizing: border-box;
      .title {
        font-size: 0.14rem;
        margin: 0.05rem;
        color: #333;
      }
      .desc {
        font-size: 0.12rem;
        margin: 0.1rem 0 0.1rem 0.05rem;
        color: #999;
      }
      .addcart {
        font-size: 0.18rem;
        color: #ff4a4a;
        span {
          color: #999;
          float: right;
          width: 0.8rem;
          height: 0.15rem;
          line-height: 0.15rem;
          text-align: center;
          font-size: 0.1rem;
          border-radius: 0.1rem;
          border: 1px solid #ff4a4a;
          position: relative;
        }
        span::after {
          content: "+";
          width: 0.15rem;
          height: 0.15rem;
          line-height: 0.13rem;
          font-size: 0.15rem;
          background: #ff4a4a;
          border-radius: 50%;
          color: white;
          right: -0.04rem;
          position: absolute;
        }
      }
      img {
        width: 100%;
        display: block;
      }
    }
  }
}

.end {
  font-size: 0.16rem;
  text-align: center;
  margin: 0.3rem 0 0.7rem 0;
}
</style>