<template>
  <transition>
    <div class="container" v-if="flag == true">
      <div class="top">
        <i @click="retuan" class="iconfont icon-gengduo"></i>账户设置
      </div>
      <div class="main">
        <van-address-edit
          :area-list="areaList"
          show-postal
          show-delete
          show-set-default
          show-search-result
          :search-result="searchResult"
          :area-columns-placeholder="['请选择', '请选择', '请选择']"
          @save="onSave"
          @delete="onDelete"
          @change-detail="onChangeDetail"
        />
      </div>
    </div>
  </transition>
</template>

<script>
import areaList from "../area";
import Toast from "vant";
export default {
  data() {
    return {
      flag: false,
      areaList,
      searchResult: []
    };
  },
  methods: {
    retuan() {
      window.history.back();  
    },
    async onSave(value) {
      let add = `${value.province} ${value.city} ${value.county}${value.addressDetail}`;
      const { data: res } = await this.$http.post("address/add", {
        username: window.sessionStorage.getItem("username"),
        name: value.name,
        tel: value.tel,
        address: add,
        postalCode: value.postalCode,
        isDefault: value.isDefault
      });
      console.log(res);
      if(res.err === 0){
        this.$router.push('/address')
      }
    },
    onDelete() {
    },
    onChangeDetail(val) {
      if (val) {
        this.searchResult = [];
      } else {
        this.searchResult = [];
      }
    }
  },
  created() {
    this.flag = true;
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

//路由切换样式
.sections .hash .router-link-exact-active {
  color: #ff4a4a;
}

</style>