<template>
  <transition>
    <div class="container" v-if="flag == true">
      <div class="top">
        <i @click="retuan" class="iconfont icon-gengduo"></i>账户与安全
      </div>
      <div class="main">
        <div class="usname swich">
          用户名
          <span>{{ userData.username }}</span>
        </div>

        <div class="swich">
          修改手机号码
          <span>
            <input type="text" @keyup.enter="setphone" v-model="userData.phone" />
            <i class="iconfont icon-gengduo"></i>
          </span>
        </div>

        <div class="swich">
          修改邮箱
          <span>
            <input type="text" @keyup.enter="setmail" v-model="userData.mail" />
            <i class="iconfont icon-gengduo"></i>
          </span>
        </div>

        <div class="swich">
          修改登录密码
          <span>
            <input type="password" @keyup.enter="setpassword" v-model="userData.password" />
            <i class="iconfont icon-gengduo"></i>
          </span>
        </div>

        <div class="swich" @click="del">
          注销账户
          <span>
            <i class="iconfont icon-gengduo"></i>
          </span>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { Toast } from "vant";
import { ImagePreview } from "vant";
export default {
  data() {
    return {
      flag: false,
      userData: []
    };
  },
  methods: {
    retuan() {
      window.history.back();  
    },

    async myData() {
      const { data: res } = await this.$http.post("user/getinfouser", {
        key: window.sessionStorage.getItem("username")
      });
      this.userData = res.list[0];
    },
    async setphone(){
      if (this.userData.userphone == "") {
        return this.$notify("不能输入空名字");
      } else {
        const { data: res } = await this.$http.post("user/upphone", {
          _id: this.userData._id,
          phone: this.userData.phone
        });
        if (res.err === 0) {
          Toast(`修改成功`);
          this.myData();
        } else {
          this.$notify("不能输入空名字");
        }
      }
    },
    async setmail(){
      if (this.userData.mail == "") {
        return this.$notify("不能输入空名字");
      } else {
        const { data: res } = await this.$http.post("user/upmail", {
          _id: this.userData._id,
          mail: this.userData.mail
        });
        if (res.err === 0) {
          Toast(`修改成功`);
          this.myData();
        } else {
          this.$notify("不能输入空名字");
        }
      }
    },
    async setpassword(){
      if (this.userData.password == "") {
        return this.$notify("不能输入空名字");
      } else {
        const { data: res } = await this.$http.post("user/uppasword", {
          _id: this.userData._id,
          password: this.userData.password
        });
        if (res.err === 0) {
          Toast(`修改成功`);
          this.myData();
        } else {
          this.$notify("不能输入空名字");
        }
      }
    },
    async del(){
      const { data: res } = await this.$http.post("user/del", {
        _id: this.userData._id,
      });
      if (res.err === 0) {
        Toast(`删除成功`);
        this.$router.push('/login')
      } else {
        Toast(`删除失败`);
      }
    }
  },
  created() {
    this.flag = true;
    this.myData();
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  opacity: 0.5;
  transform: translateX(3.75rem);
}
.v-enter-active {
  transition: transform 0.3s;
}

.v-leave {
  opacity: 0.5;
}
.v-leave-active {
  transform: translateX(3.75rem);
  transition: transform 0.3s;
}

//路由切换样式
.sections .hash .router-link-exact-active {
  color: #ff4a4a;
}

.swich {
  width: 100%;
  display: flex;
  height: 0.4rem;
  padding: 0 0.12rem;
  font-size: 0.16rem;
  margin-top: 0.12rem;
  align-items: center;
  background: white;
  box-sizing: border-box;
  border-radius: 0.08rem;
  justify-content: space-between;
  span {
    color: #666;
  }
  input {
    width: 1.1rem;
    border: none;
  }
}
</style>