<template>
  <van-pull-refresh v-model="isLoading" @refresh="onRefresh">
    <van-sticky>
      <div class="index-navbar main">
        <div>
          <van-search
            class="search"
            v-model="search"
            shape="round"
            @change="searchClass"
            background="#ff4a4a"
            placeholder="搜索商品类目"
          />
        </div>
      </div>
      <div class="middle">
        <div class="asider">
          <div
            v-for="(item,index) in classData"
            :key="index"
            :class="{active:index == current}"
            @click="selectClass(index)"
          >{{ item.typename }}</div>
        </div>
        <div class="content">
          <div class="hot">
            <p class="lines">热销</p>
            <div class="hot-box">
              <div v-for="(item,index) in hot" :key="index" @click="seedetails(hot,index)">
                <img :src="item.img" alt />
                <p>{{ item.name }}</p>
              </div>
            </div>
          </div>
          <div class="sale">
            <p class="saleLine">特价卖场</p>
            <div class="hot-box sale-box">
              <div v-for="(item,index) in sale" :key="index" @click="seedetails(sale,index)">
                <img :src="item.img" alt />
                <p>{{ item.name }}</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </van-sticky>
    <Menu></Menu>
  </van-pull-refresh>
</template>

<script>
import Menu from "../components/Menu";

export default {
  data() {
    return {
      isLoading: false,
      search: "",
      current: 0,
      classData: [],
      hot: [],
      sale: []
    };
  },
  methods: {
    onRefresh() {
      this.getClassList();
      setTimeout(() => {
        Toast("刷新成功");
        this.isLoading = false;
      }, 1000);
    },

    async searchClass() {
      const { data: res } = await this.$http.post("product/getinfotype", {
        typename: this.search
      });
    },

    //选择分类
    selectClass(index = 0) {
      this.current = index;
      this.getClassList();
    },

    async getClassList() {
      const { data: res } = await this.$http.post("classify/all");
      if (res.err !== 0) return;
      this.classData = res.list;
      this.hotBox(this.classData[this.current].typename);
      this.saleBox(this.classData[this.current].typename);
    },

    //通过编程式导航跳转到商品详情
    seedetails(row, index) {
      let token = window.sessionStorage.getItem("token");
      if (!token) {
        this.$notify("请先登录");
      } else {
        this.$router.push(`/details/1/${row[index]._id}`);
      }
    },

    async hotBox(row) {
      const { data: res } = await this.$http.post("product/classpage", {
        typename: row,
        pagesize: 4,
        page: 1
      });
      this.hot = res.info.list;
    },

    async saleBox(row) {
      const { data: res } = await this.$http.post("product/classpage", {
        typename: row,
        pagesize: 16,
        page: 1
      });
      this.sale = res.info.list;
    }
  },
  created() {
    this.selectClass();
    if (this.$route.params.id == 2) {
      this.getClassList();
      this.$route.params.id == 1;
    }
  },
  components: {
    Menu
  }
};
</script>

<style lang="less" scoped>
// 顶部导航
.index-navbar {
  width: 3.75rem;
  height: 0.44rem;
  background-color: #ff4a4a;
  .search {
    width: 100%;
    color: #999;
  }
}

.middle {
  margin-top: 0.12rem;

  .asider {
    .active {
      color: white;
      background: #ff4a4a;
    }
    float: left;
    height: auto;
    width: 1.04rem;
    background: white;
    border-radius: 0 0.08rem 0.08rem 0;
    > div {
      p {
        margin-bottom: 0.05rem;
      }
      color: #999;
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      height: 0.42rem;
      font-weight: bold;
      font-size: 0.15rem;
      align-items: center;
      justify-content: center;
    }
  }
  .content {
    float: right;
    width: 2.475rem;
    height: auto;
    margin: 0 0.117rem;
    > div p {
      margin-bottom: 0.1rem;
    }
    .sale-box {
      justify-content: start !important;
      div {
        width: 0.4rem;
        height: auto;
        margin: 0 0.21rem 0.12rem 0;
      }
      div:nth-child(4n) {
        margin-right: 0rem;
      }
    }
    .hot-box {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-between;
      div {
        width: 0.4rem;
        height: auto;
        background: white;
        p {
          width: 100%;
          overflow: hidden;
          font-size: 0.1rem;
          text-align: center;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
        img {
          float: left;
          width: 100%;
          margin-bottom: 0.05rem;
        }
      }
    }
    > div {
      padding: 0.12rem;
      background: white;
      box-sizing: border-box;
      margin-bottom: 0.12rem;
      border-radius: 0.08rem;
    }
    .lines {
      width: 100%;
      text-align: center;
      font-size: 0.16rem;
      position: relative;
    }
    .lines::before {
      content: "";
      height: 1px;
      width: 35%;
      top: 0.05rem;
      left: 0;
      background: #ddd;
      position: absolute;
    }
    .lines::after {
      content: "";
      height: 1px;
      width: 35%;
      top: 0.05rem;
      right: 0;
      background: #ddd;
      position: absolute;
    }
    .saleLine {
      width: 100%;
      text-align: center;
      font-size: 0.16rem;
      position: relative;
    }
    .saleLine::before {
      content: "";
      height: 1px;
      width: 30%;
      top: 0.05rem;
      left: 0;
      background: #ddd;
      position: absolute;
    }
    .saleLine::after {
      content: "";
      height: 1px;
      width: 30%;
      top: 0.05rem;
      right: 0;
      background: #ddd;
      position: absolute;
    }
  }
}
</style>