<template>
  <div>
    <div class="top">
      <div class="logobg">
        <img class="logo" src="../../public/images/logo.png" alt />
      </div>
    </div>

    <div class="sections">
      <div class="hash">
        <div>
          <router-link to="login">登录</router-link>
        </div>
        <div>
          <router-link to="reg">注册</router-link>
        </div>
      </div>
      <transition>
        <div class="froms" v-if="flag == true">
          <form action>
            <input type="text" v-model="regForm.user" @blur="us" placeholder="邮箱注册" />
            <input type="password"  autocomplete=’tel’ v-model="regForm.password" @blur="ps" placeholder="密码" />
            <div class="code">
              <input type="text" placeholder="验证码" @focus="codes" v-model="regForm.code" />
              <button @click="code">{{ codeContent }}</button>
            </div>
            <div>
              <button @click="reg">登录</button>
            </div>
            <div class="but">
              <div style="color:#ff4a4a">
                <input type="checkbox" checked />
                <span style="color:#999">同意</span>服务条款 网络安全条款...
              </div>
              <span style="color:#999;">其他</span>
            </div>
          </form>
        </div>
      </transition>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      flag: false,
      regForm: {
        user: "",
        password: "",
        code: ""
      },
      codeContent: "点击获取验证码",
      count: true
    };
  },
  methods: {
    // 用户名的正则验证
    us() {
      if (this.regForm.user === "") {
        this.$notify("请输入账号");
      } else {
        this.refs = 1;
      }
    },

    //密码框的正则验证
    ps() {
      let reg = "^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,12}$";
      let req = new RegExp(reg);
      if (this.regForm.password === "") {
        this.$notify("请输入密码");
      } else if (!req.test(this.regForm.password)) {
        this.$notify("密码必须6-12位，数字字母组成");
      } else {
        this.ref = 1;
      }
    },

    // 用户名的正则验证
    codes() {
      if (this.regForm.code === "") {
        this.$notify("请输入验证码");
      } else {
        this.refs = 1;
      }
    },

    //点击获取验证码
    code() {
      if(this.regForm.user === '') {
        this.$notify("请输入邮箱");
      }else {
        if (this.count == true) {
        this.count = false;
        this.codeContent = 60;
        this.$http.post("user/getmail", {
          mail: this.regForm.user
        });
        this.timer = setInterval(() => {
          this.codeContent--;
          if (this.codeContent == 0) {
            this.count = true;
            clearInterval(this.timer);
            this.codeContent = "点击获取验证码";
          }
        }, 1000);
      }
      }
    },

    //注册
    async reg() {
      const { data: res } = await this.$http.post("user/reg", {
          username: this.regForm.user,
          mail: this.regForm.user,
          password: this.regForm.password,
          code: this.regForm.code
        });
        console.log(res)
        //通过es6结构赋值的方法来获取想要的属性
        if (res.err !== 0) return  this.$notify("用户名已存在或者邮箱已经注册过一次了");
        this.$notify({ type: 'success', message: '注册成功' });
        // 2.通过编程式导航跳转到主页
        this.$router.push("/login");
    }
  },
  created() {
    this.flag = true;
  }
};
</script>

<style lang="less" scoped>
.v-enter {
  transform: translateX(200px);
}
.v-enter-active {
  transition: transform 0.3s;
}

//路由切换样式
.sections .hash .router-link-exact-active {
  color: #ff4a4a;
}

.top {
  width: 3.75rem;
  height: 2rem;
  background: url(../../public/images/loginbg.png);
  background-size: cover;
  position: relative;
  margin-bottom: 0.625rem;
  .logobg {
    bottom: -0.42rem;
    left: 50%;
    margin-left: -0.42rem;
    width: 0.84rem;
    height: 0.84rem;
    line-height: 0.74rem;
    background: white;
    position: absolute;
    border-radius: 50%;
    box-shadow: 0px 2px 10px rgba(0, 0, 0, 0.3);
    .logo {
      width: 100%;
      // height: 85%;
    }
  }
}

.sections {
  padding: 0 0.52rem 0.32rem 0.52rem;
  .hash {
    display: flex;
    font-size: 0.15rem;
    margin-bottom: 0.08rem;
    a {
      color: #999;
      font-family: "微软雅黑";
      font-weight: 600;
    }
    div {
      width: 1.355rem;
      text-align: center;
    }
  }

  .froms {
    font-size: 0.1rem;
    padding: 0.285rem 0;
    input {
      color: #999;
      padding: 0;
      width: 2.71rem;
      height: 0.36rem;
      border: none;
      outline: none;
      text-indent: 0.235rem;
      border-radius: 0.18rem;
      margin-bottom: 0.16rem;
      box-shadow: 2px 2px 10px rgba(0, 0, 0, 0.3);
    }
    button {
      color: white;
      font-size: 0.16rem;
      width: 100%;
      height: 0.36rem;
      border: none;
      outline: none;
      margin: 0.08rem 0 0.12rem 0;
      border-radius: 0.18rem;
      background: #ff4a4a;
    }
    .code {
      display: flex;
      margin-bottom: 0.2rem;
      justify-content: space-between;
      input {
        width: 1.36rem;
        margin: 0;
      }
      button {
        margin: 0;
        font-size: 0.12rem;
        width: 1.05rem;
      }
    }
    .but {
      font-size: 0.08rem;
      display: flex;
      cursor: pointer;
      justify-content: space-between;
      input {
        width: 0.1rem;
        height: 0.1rem;
        border-radius: 0.09rem;
        border: 1px solid #ff4a4a;
        background: #ff4a4a;
      }
      span:first-child {
        color: #ff4a4a;
      }
    }
  }
}
</style>